<template><section><html><head></head><body><h1>CorridorGeometry, CorridorOutlineGeometry</h1>
<ul>
<li>The <code v-pre="">vc-geometry-corridor</code> component is used to load a geometry that describes a corridor.</li>
<li>The <code v-pre="">vc-geometry-outline-corridor</code> component is used to load a geometry that describes a corridor outline.</li>
<li>It needs to be used as a sub-component of <code v-pre="">vc-instance-geometry</code>, can be mounted on <code v-pre="">vc-primitive</code> or <code v-pre="">vc-primitive-ground</code>.</li>
</ul>
<h2>Example</h2>
<h3>Load CorridorGeometry</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" @left_click="LEFT_CLICK">
        <vc-primitive-ground :appearance="appearance">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes">
            <vc-geometry-corridor ref="corridor" :positions="positions" :width="width"></vc-geometry-corridor>
          </vc-instance-geometry>
        </vc-primitive-ground>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributesOutline">
            <vc-geometry-outline-corridor ref="corridorOutline" :positions="positionsOutline" :width="width"></vc-geometry-outline-corridor>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
