<h2>Instance Properties</h2>
<h3><code v-pre="">vc-geometry-corridor</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre="">required</code> An array of positions that define the center of the corridor. <strong>Structure[{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td></td>
<td><code v-pre="">required</code> The distance between the edges of the corridor in meters.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The ellipsoid to be used as a reference.</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> The distance, in radians, between each latitude and longitude. Determines the number of positions in the buffer.</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> The distance in meters between the ellipsoid surface and the positions.</td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> The distance in meters between the ellipsoid surface and the extruded face.</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The vertex attributes to be computed.</td>
</tr>
<tr>
<td>cornerType</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> Determines the style of the corners. <strong>ROUNDED: 0, MITERED: 1, BEVELED: 2</strong></td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre="">vc-geometry-outline-corridor</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>positions</td>
<td>Array</td>
<td></td>
<td><code v-pre="">required</code> An array of positions that define the center of the corridor outline. <strong>Structure[{ lng: number, lat: number, height: number },...,{ lng: number, lat: number, height: number }]</strong></td>
</tr>
<tr>
<td>width</td>
<td>Number</td>
<td></td>
<td><code v-pre="">required</code> 	The distance between the edges of the corridor outline.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> The ellipsoid to be used as a reference.</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> The distance, in radians, between each latitude and longitude. Determines the number of positions in the buffer.</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> The distance in meters between the ellipsoid surface and the positions.</td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> The distance in meters between the ellipsoid surface and the extruded face.</td>
</tr>
<tr>
<td>cornerType</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> Determines the style of the corners. <strong>ROUNDED: 0, MITERED: 1, BEVELED: 2</strong></td>
</tr>
</tbody>
</table>
<hr>
<p>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/CorridorGeometry.html">CorridorGeometry</a></strong>, <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/CorridorOutlineGeometry.html">CorridorOutlineGeometry</a></strong></p>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          geometry: null,
          width: 250000,
          attributes: null,
          attributesOutline: null,
          positions: [
            { lng: 100.0, lat: 40.0 },
            { lng: 105.0, lat: 40.0 },
            { lng: 105.0, lat: 35.0 }
          ],
          positionsOutline: [
            { lng: 102.0, lat: 40.0 },
            { lng: 107.0, lat: 40.0 },
            { lng: 107.0, lat: 35.0 }
          ]
        }
      },
      mounted() {
        Promise.all([this.$refs.corridor.createPromise, this.$refs.corridorOutline.createPromise]).then((instances) => {
          console.log('All geometries are loaded.')
          const boundingSphereUnion = instances.reduce((prev, cur) => {
            const geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
            const boundingSphere = cur.vm.$parent.modelMatrix
              ? Cesium.BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
              : geometry.boundingSphere
            return prev === null ? boundingSphere : Cesium.BoundingSphere.union(prev, boundingSphere)
          }, null)
          instances[0].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
        })
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.viewer = viewer
          const { PerInstanceColorAppearance, ColorGeometryInstanceAttribute } = Cesium
          this.appearance = new PerInstanceColorAppearance({
            flat: true
          })
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(1.0, 1.0, 0.0, 1.0)
          }
          this.attributesOutline = {
            color: new ColorGeometryInstanceAttribute(1.0, 0.0, 0.0, 1.0)
          }
        },
        LEFT_CLICK(movement) {
          const feature = this.viewer.scene.pick(movement.position)
          console.log(feature)
        }
      }
    }
  </script>